import React, { useState, useEffect } from "react";

import { makeStyles, withStyles } from '@material-ui/core/styles';
import { useHistory } from "react-router";
import {
    Card, CssBaseline, CardHeader, CardMedia, Grid, CardContent, CardActions, Collapse
    , Avatar, Button, Typography, Container, IconButton, List, ListSubheader, ListItemIcon, ListItem, ListItemText
} from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        textAlign: 'center',
        justifyContent: 'center',
        paddingTop: '2rem',
        overflow: 'hidden',
        background: 'linear-gradient(45deg,#e0e0e0,transparent)',
        height: '100vh',
        width: 'auto'
    },
    mainHeader: {
        fontSize: '3rem',
    },
    button: {
        background: " linear-gradient(180deg, #212121 0%, #010101 100%) 0% 0% ",
        height: 50,
        width: 202,
        borderRadius: 60,
        "&:hover": {
            background: " linear-gradient(180deg, #010101 0%, #212121 100%) 0% 0% ",
        },
        backgroundColor: '#212121 !important'
    },
    gridItem: {
        margin: '10px 10px 20px 15px',
        minWidth: '252px'
    }
}));

export default function Main(props) {

    useEffect(() => {

    }, []);
    const classes = useStyles();
    const history = useHistory();

    const nextPath = (path) => {
        history.push(path);
    };

    return (
        <React.Fragment>
            <>
                <Container maxWidth={'lg'} style={{ paddingLeft: '0px', paddingRight: '0px' }}>
                    <Card className={classes.root} raised elevation={0} >
                        <Typography className={classes.mainHeader}>
                            <strong>
                                Mathamatical <br /> Calculations
                </strong></Typography>
                        <CardContent class='appContainer'>
                            <Grid container justify="space-around">
                                <Button
                                    variant="contained"
                                    color="secondary"
                                    className={classes.button + ' ' + classes.gridItem}
                                >Get Start</Button>
                            </Grid>

                        </CardContent>
                    </Card>
                </Container>
            </>
        </React.Fragment >
    );
}
